.TestimonialSec {
  text-align: center;
  width: 80vw;
  margin: 0 auto;
}
.TestimonialSec h2 {
  text-transform: uppercase;
  margin: 5rem 0;
  font-size: 1.2rem;
  color: hsl(210, 4%, 67%);
  letter-spacing: 0.2rem;
}
.testimonials {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}
@media only screen and (max-width: 770px) {
  .TestimonialSec {
    width: 90vw;
  }
  .testimonials {
    display: unset;
  }
}
